// TinyScroller.js

import React, { useEffect, useState } from 'react';
import './TinyScroller.css';

const TinyScroller = () => {
  const [scrollPercentage, setScrollPercentage] = useState(0);

  const handleScroll = () => {
    const container = document.getElementById('scrollContainer');
    const percentage = (container.scrollTop / (container.scrollHeight - container.clientHeight)) * 100;
    setScrollPercentage(percentage);
  };

  useEffect(() => {
    const container = document.getElementById('scrollContainer');
    container.addEventListener('scroll', handleScroll);

    return () => {
      container.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <div className="scroll-container" id="scrollContainer">
      <div className="content">
        {/* Your website content goes here */}
        FOR  INFO UPDATE
      </div>
      <div className="scroller" style={{ height: `${scrollPercentage}%` }}></div>
    </div>
  );
};

export default TinyScroller;